									<div style="font-size: 12px;text-align: center;margin-bottom: 5px;">‰∏ÄÊØõÊòØÈºìÂä±</div>
									<img :src="$store.state.siteInfo.reward" alt="" class="ui rounded bordered image" style="width: 100%">
									<div style="font-size: 12px;text-align: center;margin-top: 5px;">‰∏ÄÂùóÊòØÁúüÁà±</div>
								</div>
