		</div>
    <!-- ÂÖ®Â±èÊ®°Âºè‰∏ãÁöÑÁõÆÂΩï -->
		<div class="fullscreen-toc" v-if="isFullscreen" :class="{'toc-hidden': isTocHidden}">
			<div class="toc-toggle" @click="toggleToc">
				<i :class="isTocHidden ? 'angle double right icon' : 'angle double left icon'"></i>
			</div>
			<div class="toc-content">
				<div class="ui segments m-toc toc-wrapper m-box">
					<div class="ui secondary segment">
						<i class="list ul icon"></i>Êú¨ÊñáÁõÆÂΩï
					</div>
					<div class="ui yellow segment">
						<div class="js-toc-fullscreen"></div>
					</div>
				</div>
			</div>
		</div>
		<!--ÂçöÂÆ¢‰ø°ÊÅØ-->
		<div class="ui attached positive message">
			<ul class="list">
				<li>‰ΩúËÄÖÔºö{{ $store.state.introduction.name }}
					<router-link to="/about">ÔºàËÅîÁ≥ª‰ΩúËÄÖÔºâ</router-link>
				</li>
				<li>ÂèëË°®Êó∂Èó¥Ôºö{{ blog.createTime | dateFormat('YYYY-MM-DD HH:mm') }}</li>
				<li>ÊúÄÂêé‰øÆÊîπÔºö{{ blog.updateTime | dateFormat('YYYY-MM-DD HH:mm') }}</li>
				<li>Êú¨Á´ôÁÇπÈááÁî®<a href="https://creativecommons.org/licenses/by/4.0/" target="_blank"> ÁΩ≤Âêç 4.0 ÂõΩÈôÖ (CC BY 4.0) </a>Âàõ‰ΩúÂÖ±‰∫´ÂçèËÆÆ„ÄÇÂèØËá™Áî±ËΩ¨ËΩΩ„ÄÅÂºïÁî®ÔºåÂπ∂‰∏îÂÖÅËÆ∏ÂïÜ‰∏öÊÄß‰ΩøÁî®„ÄÇ‰ΩÜÈúÄÁΩ≤Âêç‰ΩúËÄÖ‰∏îÊ≥®ÊòéÊñáÁ´†Âá∫Â§Ñ„ÄÇ</li>
			</ul>
		</div>
		<!--ËØÑËÆ∫-->
		<div class="ui bottom teal attached segment threaded comments">
			<CommentList :page="0" :blogId="blogId" v-if="blog.commentEnabled"/>
			<h3 class="ui header" v-else>ËØÑËÆ∫Â∑≤ÂÖ≥Èó≠</h3>
		</div>
	</div>
</template>

<script>
	import {getBlogById} from "@/api/blog";
	import CommentList from "@/components/comment/CommentList";
	import {mapState} from "vuex";
	import {SET_FOCUS_MODE, SET_IS_BLOG_RENDER_COMPLETE} from '@/store/mutations-types';

	export default {
		name: "Blog",
		components: {CommentList},
		data() {
			return {
				blog: {},
				bigFontSize: false,
        isZoomed: false,
        zoomedImage: null,
        scrollTimeout: null,
        isFullscreen: false,
        isTocHidden: false
			}
		},
		computed: {
			blogId() {
				return parseInt(this.$route.params.id)
			},
			...mapState(['siteInfo', 'focusMode'])
		},
		beforeRouteEnter(to, from, next) {
			//Ë∑ØÁî±Âà∞ÂçöÂÆ¢ÊñáÁ´†È°µÈù¢‰πãÂâçÔºåÂ∫îÂ∞ÜÊñáÁ´†ÁöÑÊ∏≤ÊüìÂÆåÊàêÁä∂ÊÄÅÁΩÆ‰∏∫ false
			next(vm => {
				// ÂΩì beforeRouteEnter Èí©Â≠êÊâßË°åÂâçÔºåÁªÑ‰ª∂ÂÆû‰æãÂ∞öÊú™ÂàõÂª∫
				// vm Â∞±ÊòØÂΩìÂâçÁªÑ‰ª∂ÁöÑÂÆû‰æãÔºåÂèØ‰ª•Âú® next ÊñπÊ≥ï‰∏≠Êää vm ÂΩìÂÅö thisÁî®
				vm.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, false)
			})
		},
		beforeRouteLeave(to, from, next) {
			this.$store.commit(SET_FOCUS_MODE, false)
			// ‰ªéÊñáÁ´†È°µÈù¢Ë∑ØÁî±Âà∞ÂÖ∂ÂÆÉÈ°µÈù¢Êó∂ÔºåÈîÄÊØÅÂΩìÂâçÁªÑ‰ª∂ÁöÑÂêåÊó∂ÔºåË¶ÅÈîÄÊØÅtocbotÂÆû‰æã
			// Âê¶Âàôtocbot‰∏ÄÁõ¥Âú®ÁõëÂê¨È°µÈù¢ÊªöÂä®‰∫ã‰ª∂ÔºåËÄåÊñáÁ´†È°µÈù¢ÁöÑÈîöÁÇπÂ∑≤Áªè‰∏çÂ≠òÂú®‰∫ÜÔºå‰ºöÊä•"Uncaught TypeError: Cannot read property 'className' of null"
			tocbot.destroy()
			next()
		},
		beforeRouteUpdate(to, from, next) {
			// ‰∏ÄËà¨Êúâ‰∏§ÁßçÊÉÖÂÜµ‰ºöËß¶ÂèëËøô‰∏™Èí©Â≠ê
			// ‚ë†ÂΩìÂâçÊñáÁ´†È°µÈù¢Ë∑≥ËΩ¨Âà∞ÂÖ∂ÂÆÉÊñáÁ´†È°µÈù¢
			// ‚ë°ÁÇπÂáªÁõÆÂΩïË∑≥ËΩ¨ÈîöÁÇπÊó∂ÔºåË∑ØÁî±hashÂÄº‰ºöÊîπÂèòÔºåÂØºËá¥ÂΩìÂâçÈ°µÈù¢‰ºöÈáçÊñ∞Âä†ËΩΩÔºåËøôÁßçÊÉÖÂÜµÊòØ‰∏çÂ∏åÊúõÂá∫Áé∞ÁöÑ
			// Âú®Ë∑ØÁî± beforeRouteUpdate ‰∏≠Âà§Êñ≠Ë∑ØÂæÑÊòØÂê¶ÊîπÂèò
			// Â¶ÇÊûúË∑≥ËΩ¨Âà∞ÂÖ∂ÂÆÉÈ°µÈù¢Ôºåto.path!==from.path Â∞±ÊîæË°å next()
			// Â¶ÇÊûúÊòØË∑≥ËΩ¨ÈîöÁÇπÔºåpath‰∏ç‰ºöÊîπÂèòÔºåhash‰ºöÊîπÂèòÔºåto.path===from.path, to.hash!==from.path ‰∏çÊîæË°åË∑ØÁî±Ë∑≥ËΩ¨ÔºåÂ∞±ËÉΩËÆ©ÈîöÁÇπÊ≠£Â∏∏Ë∑≥ËΩ¨
			if (to.path !== from.path) {
				this.$store.commit(SET_FOCUS_MODE, false)
				//Âú®ÂΩìÂâçÁªÑ‰ª∂ÂÜÖË∑ØÁî±Âà∞ÂÖ∂ÂÆÉÂçöÂÆ¢ÊñáÁ´†Êó∂ÔºåË¶ÅÈáçÊñ∞Ëé∑ÂèñÊñáÁ´†
				this.getBlog(to.params.id)
				//Âè™Ë¶ÅË∑ØÁî±Ë∑ØÂæÑÊúâÊîπÂèòÔºå‰∏îÂÅúÁïôÂú®ÂΩìÂâçBlogÁªÑ‰ª∂ÂÜÖÔºåÂ∞±ÊääÊñáÁ´†ÁöÑÊ∏≤ÊüìÂÆåÊàêÁä∂ÊÄÅÁΩÆ‰∏∫ false
				this.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, false)
				next()
			}
		},
		created() {
			this.getBlog()
		},
		methods: {
			getBlog(id = this.blogId) {
				//ÂØÜÁ†Å‰øùÊä§ÁöÑÊñáÁ´†ÔºåÈúÄË¶ÅÂèëÈÄÅÂØÜÁ†ÅÈ™åËØÅÈÄöËøáÂêé‰øùÂ≠òÂú®localStorageÁöÑToken
				const blogToken = window.localStorage.getItem(`blog${id}`)
				//Â¶ÇÊûúÊúâÂàôÂèëÈÄÅÂçö‰∏ªË∫´‰ªΩToken
				const adminToken = window.localStorage.getItem('adminToken')
				const token = adminToken ? adminToken : (blogToken ? blogToken : '')
				getBlogById(token, id).then(res => {
					if (res.code === 200) {
						this.blog = res.data
						document.title = this.blog.title + this.siteInfo.webTitleSuffix
						//v-htmlÊ∏≤ÊüìÂÆåÊØïÂêéÔºåÊ∏≤Êüì‰ª£Á†ÅÂùóÊ†∑Âºè
						this.$nextTick(() => {
							Prism.highlightAll()
							//Â∞ÜÊñáÁ´†Ê∏≤ÊüìÂÆåÊàêÁä∂ÊÄÅÁΩÆ‰∏∫ true
							this.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, true)
						})
					} else {
						this.msgError(res.msg)
					}
				}).catch(() => {
					this.msgError("ËØ∑Ê±ÇÂ§±Ë¥•")
				})
			},
			changeFocusMode() {
				this.$store.commit(SET_FOCUS_MODE, !this.focusMode)
			},
      handleImageZoom(event) {
        const img = event.target;
        if (img.tagName.toLowerCase() === 'img') {
          // Reset previous zoomed image if exists
          if (this.zoomedImage && this.zoomedImage !== img) {
            this.resetZoom(this.zoomedImage);
          }

          if (!this.isZoomed || this.zoomedImage !== img) {
            // Zoom in
            this.zoomImage(img);
          } else {
            // Zoom out
            this.resetZoom(img);
          }
        }
      },
      // ÂàáÊç¢ÂõæÁâáÊîæÂ§ßÊïàÊûú
      zoomImage(img) {
        // Calculate the viewport center
        const viewportHeight = window.innerHeight;
        const viewportWidth = window.innerWidth;

        // Get image dimensions and position
        const rect = img.getBoundingClientRect();

        // Calculate the scale factor (max 2.5x)
        const scaleX = Math.min(viewportWidth / rect.width * 0.8, 2.5);
        const scaleY = Math.min(viewportHeight / rect.height * 0.8, 2.5);
        const scale = Math.min(scaleX, scaleY);

        // Apply transforms
        img.style.transform = `scale(${scale})`;
        img.style.zIndex = '1000';
        img.style.position = 'relative';

        // Update state
        this.isZoomed = true;
        this.zoomedImage = img;

        // Add zoomed class for additional styling
        img.classList.add('zoomed');
      },
      resetZoom(img) {
        if (img) {
          img.style.transform = 'scale(1)';
          img.style.zIndex = 'auto';
          img.style.position = 'static';
          img.classList.remove('zoomed');
        }
        this.isZoomed = false;
        this.zoomedImage = null;
      },
      handleScroll() {
        // Clear the previous timeout
        if (this.scrollTimeout) {
          clearTimeout(this.scrollTimeout);
        }

        // Set a new timeout
        this.scrollTimeout = setTimeout(() => {
          if (this.isZoomed && this.zoomedImage) {
            this.resetZoom(this.zoomedImage);
          }
        }, 150); // Debounce time
      },
      setupImageHandling() {
        const blogContent = this.$refs.blogContent;
        if (blogContent) {
          const images = blogContent.getElementsByTagName('img');
          Array.from(images).forEach(img => {
            img.style.cursor = 'pointer';
            // Ensure images are above other content when zoomed
            img.addEventListener('click', this.handleImageZoom);
          });
        }
      },
      toggleFullscreen() {
        this.isFullscreen = !this.isFullscreen;
        if (this.isFullscreen) {
          document.body.style.overflow = 'hidden';
          // ÊªöÂä®Âà∞È°∂ÈÉ®
          window.scrollTo(0, 0);
          // ÂàùÂßãÂåñÂÖ®Â±èÊ®°Âºè‰∏ãÁöÑÁõÆÂΩï
          this.$nextTick(() => {
            this.initFullscreenTocbot();
            // ÊÅ¢Â§ç‰∏äÊ¨°ÁöÑÁõÆÂΩïÊòæÁ§∫Áä∂ÊÄÅ
            const savedTocHidden = localStorage.getItem('tocHidden');
            if (savedTocHidden !== null) {
              this.isTocHidden = savedTocHidden === 'true';
            }
          });
        } else {
          document.body.style.overflow = 'auto';
          // ‰øùÂ≠òÁõÆÂΩïÊòæÁ§∫Áä∂ÊÄÅ
          localStorage.setItem('tocHidden', this.isTocHidden);
          // ÈîÄÊØÅÂÖ®Â±èÊ®°Âºè‰∏ãÁöÑÁõÆÂΩï
          tocbot.destroy();
          // ÈáçÊñ∞ÂàùÂßãÂåñÊ≠£Â∏∏Ê®°Âºè‰∏ãÁöÑÁõÆÂΩï
          this.$nextTick(() => {
            this.initNormalTocbot();
          });
        }
      },
      toggleToc() {
        this.isTocHidden = !this.isTocHidden;
        // ‰øùÂ≠òÁõÆÂΩïÊòæÁ§∫Áä∂ÊÄÅ
        localStorage.setItem('tocHidden', this.isTocHidden);
      },
      initFullscreenTocbot() {
        tocbot.init({
          tocSelector: '.js-toc-fullscreen',
          contentSelector: '.js-toc-content',
          headingSelector: 'h1,h2,h3,h4',
          scrollSmooth: true,
          scrollSmoothDuration: 420,
          scrollSmoothOffset: -55,
          headingsOffset: -18
        });
      },
      initNormalTocbot() {
        // Ëß¶Âèë Tocbot ÁªÑ‰ª∂ÈáçÊñ∞ÂàùÂßãÂåñ
        this.$store.commit(SET_IS_BLOG_RENDER_COMPLETE, true);
      }
		},
    mounted() {
      // Setup image handling after content is loaded
      this.$nextTick(() => {
        this.setupImageHandling();
      });

      window.addEventListener('scroll', this.handleScroll);

      // Êñ∞Â¢û ESC ÈîÆÁõëÂê¨
      window.addEventListener('keydown', this.handleKeydown);
    },
    updated() {
      // Refresh image handling when content updates
      this.$nextTick(() => {
        this.setupImageHandling();
      });
    },
    beforeDestroy() {

      // Ê∏ÖÁêÜÂÖ®Â±èÁä∂ÊÄÅ
      if (this.isFullscreen) {
        document.body.style.overflow = 'auto';
      }

      // Clean up
      window.removeEventListener('scroll', this.handleScroll);

      const blogContent = this.$refs.blogContent;
      if (blogContent) {
        const images = blogContent.getElementsByTagName('img');
        Array.from(images).forEach(img => {
          img.removeEventListener('click', this.handleImageZoom);
        });
      }
    }
  }
</script>

