  }

  /* ÂÖ®Â±èÊ®°Âºè‰∏ãÁöÑÂÜÖÂÆπÂÆπÂô® */
  .fullscreen-container .ui.padded.attached.segment {
    background: rgba(255, 255, 255, 0.95);
    backdrop-filter: blur(10px);
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    width: 100vw;
    border: 1px solid rgba(255, 255, 255, 0.2);
    transition: all 0.3s ease;
  }

  /* ÁõÆÂΩïÈöêËóèÊó∂ÊñáÁ´†ÂÜÖÂÆπÂÖ®Â±è */
  .fullscreen-container .fullscreen-toc.toc-hidden + .ui.padded.attached.segment {
    width: 100vw;
    margin-left: 0;
  }

  /* ÂìçÂ∫îÂºèË∞ÉÊï¥ */
  @media (max-width: 768px) {
    .fullscreen-container {
      padding: 10px;
    }

    .fullscreen-container .typo {
      padding: 1rem;
      font-size: 14px;
    }
  }

  /* ÂÖ®Â±èÊ®°Âºè‰∏ãÁöÑÁõÆÂΩïÊ†∑Âºè */
  .fullscreen-toc {
    position: fixed;
    top: 60px;
    left: 0;
    width: 300px;
    height: calc(100vh - 60px);
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 5px 0 15px rgba(0, 0, 0, 0.1);
    z-index: 10000;
    transition: transform 0.3s ease;
    overflow-y: auto;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 0 10px 10px 0;
  }

  .fullscreen-toc.toc-hidden {
    transform: translateX(-290px);
  }

  .toc-toggle {
    position: absolute;
    top: 50%;
    right: 0; /* Êîπ‰∏∫Âè≥‰æß */
    width: 20px;
    height: 50px;
    background: #08cafb;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 0 5px 5px 0; /* ‰øÆÊîπÂúÜËßí‰ΩçÁΩÆ */
    transform: translateY(-50%);
    color: white;
    font-size: 16px;
  }
</style>
